<template>
    <div class="f-giraffeApp">
        <div class="l-singleTest">
            <p>VIP売上入力</p>
             <router-link to="/">HOME</router-link>
             <ul><li v-for="user in users" :key='user'>{{ user.name }}</li></ul>
             
        </div>
    </div>
</template>

<script>
import axios from 'axios'
export default {
  data: function () {
    return {
      users:[]
    }
  },
  mounted :function(){
    axios.get('https://jsonplaceholder.typicode.com/users')
          .then(response => this.users = response.data)
          .catch(response => console.log(response))
  }
}
</script>

<style>
.f-giraffeApp{
	width: 100vw;
	height: 100vh;
    font-family: 'Noto Sans JP', sans-serif;
}

.l-singleTest{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
	justify-content: center;
	align-items: center;
}
</style>